.home #primary,
.archive #primary {
	width: 100%;
}

@media (min-width: 600px) {
	.home #post-wrap,
	.archive #post-wrap {
		column-count: 2;
		column-gap: 20px;
	}
}

@media (min-width: 900px) {
	.home #post-wrap,
	.archive #post-wrap {
		column-count: 3;
		column-gap: 40px;
	}
}

.home article,
.archive article {
	font-size: 80%;
}

.home article .featured-image,
.archive article .featured-image {
	width: 100%;
	max-width: 100%;
	margin-left: 0;
}

.home article .entry-title,
.archive article .entry-title {
	font-size: 20px;
}

body:not(.home) .site-identity {
	padding: 1% 0;
}

.entry-header .entry-title a:hover {
	box-shadow: none;
}

.footer-bottom {
	text-align: center;
}

.footer-bottom .site-info {
	font-size: 90%;
}
